<template>
  <div class="relevant">
    <div class="warp-header">
      <div class="songlistheader">
        <div class="mask">
          <img :src="playlist.coverImgUrl" alt />
        </div>
        <div class="mains">
          <div class="theme">
            <img :src="playlist.coverImgUrl" alt />
            <div class="count">
              <i class="iconfont icon-earphone"></i>
              <span>{{ translateCount(playlist.playCount) }}</span>
            </div>
            <span class="type">歌单</span>
          </div>
          <div class="text">
            <h2>{{ playlist.name }}</h2>
            <div class="creater">
              <div class="Cpic">
                <img :src="playlist.creator.avatarUrl" alt />
              </div>
              <span>{{ playlist.creator.nickname }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="introduce">
        <div class="tag">
          标签：
          <span v-for="(item, index) in playlist.tags" :key="index">{{ item }}</span>
        </div>
        <p :class="{active : isActive}">简介：{{ playlist.description }}</p>
        <div class="ishowmore">
          <i v-if="!isActive" @click="isActive = !isActive">↓</i>
          <i v-else @click="isActive = !isActive">↑</i>
        </div>
      </div>
    </div>
    <div class="entry">歌曲列表</div>
  </div>
</template>

<script>
export default {
  props: ["playlist"],
  data() {
    return {
      isActive: false
    };
  },

  methods: {
    translateCount(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(1) + "亿";
      }
      if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      }
    }
  }
};
</script>

<style lang="less" scoped>
.warp-header {
  background: #f8f8f8;
  .songlistheader {
    position: relative;
    padding: 30px 15px;
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background-color: rgba(0, 0, 0, 0.25);
    }
    .mask {
      z-index: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      img {
        width: 100%;
        filter: blur(25px);
      }
    }
    .mains {
      display: flex;
      .theme {
        position: relative;
        width: 40%;
        z-index: 2;
        img {
          vertical-align: middle;
        }
        .count {
          position: absolute;
          right: 0;
          top: 0;
          color: white;
          font-size: 12px;
          i.iconfont {
            font-size: 12px;
            margin-right: 3px;
          }
        }
        .type {
          position: absolute;
          left: 0;
          top: 10px;
          text-align: center;
          color: white;
          font-size: 12px;
          display: block;
          padding: 0 8px;
          height: 17px;
          line-height: 17px;
          background-color: rgba(217, 48, 48, 0.8);
          border-top-right-radius: 17px;
          border-bottom-right-radius: 17px;
        }
      }
      .text {
        position: relative;
        z-index: 2;
        flex: 1;
        margin-left: 15px;
        color: white;
        .creater {
          margin-top: 20px;
          display: flex;
          .Cpic {
            display: inline-block;
            width: 30px;
            border-radius: 50%;
            overflow: hidden;
            margin-right: 10px;
            img {
              width: 100%;
              vertical-align: middle;
            }
          }
          span {
            line-height: 30px;
            font-size: 14px;
          }
        }
      }
    }
  }
  .introduce {
    padding: 15px;
    color: #666;
    .tag {
      margin-bottom: 10px;
      span {
        border: 1px solid #999;
        border-radius: 20px;
        display: inline-block;
        padding: 1px 8px;
        font-size: 12px;
        margin-right: 5px;
      }
    }
    p {
      position: relative;
      font-size: 14px;
      white-space: pre-line;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      &.active {
        -webkit-line-clamp: 100;
      }
    }
    .ishowmore {
      text-align: right;
      
    }
  }
}
.entry {
  height: 23px;
  line-height: 23px;
  padding: 0 10px;
  font-size: 12px;
  color: #666;
  background-color: #eeeff0;
}
</style>