<template>
  <div class="main">
    <template v-if="isLoading">
     <div class="loading-mask">
        <van-loading color="#1989fa"/>
     </div>
    </template>
    <template v-else>
      <div class="go-back" @click="$router.go(-1)"><i class="iconfont icon-previous"></i></div>
    <SonglistItemCard v-if="playlist" :playlist="playlist"></SonglistItemCard>
    <ul class="song-list">
      <template v-if="playlist">
        <SonglistItem
          v-for="(item, index) in playlist.tracks"
          :key="index"
          :playlist="item"
          :songarr = "playlist.tracks"
          :index="index"
          @setSongId="$emit('setSongId',$event)"
        ></SonglistItem>
      </template>
    </ul>
    </template>
  </div>
</template>

<script>
import SonglistItem from "../components/SongListItem.vue";
import SonglistItemCard from "../components/SongListItemCard.vue";
export default {
  data() {
    return {
      playlist: null,
      avatarUrl:'',
      isLoading : true
    };
  },
  components: {
    SonglistItem,
    SonglistItemCard
  },
  methods: {
    getPlayListDetail(id) {
      this.axios
        .get("http://musicapi.leanapp.cn/playlist/detail", {
          params: {
            id: id
          }
        })
        .then(responsive => {
          ({ playlist: this.playlist } = responsive.data);
           ({ avatarUrl: this.avatarUrl } = responsive.data.playlist);
           this.isLoading = false
          window.console.log(responsive);
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.isLoading = true
      vm.getPlayListDetail(to.query.id);
    });
  }
};
</script>

<style>
.go-back {
  font-size: 20px;
  font-weight: 700;
  color: white;
  position: absolute;
  top: 0;
  left:15px;
  z-index: 2;
}
.van-loading{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%)
}
.loading-mask{
  position: relative;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.062)
}
</style>